import * as React from "react";
import {
  connect,
  MapStateToPropsParam,
  MapStateToProps,
  MapDispatchToProps
} from "react-redux";
import { selectAuth } from "../core/auth/auth.selectors";
import { CoreState } from "../core";
import { AuthState, authLogin } from "../core/auth";

const mapStateToProps: MapStateToPropsParam<
  { auth: AuthState },
  {},
  CoreState
> = state => ({
  auth: selectAuth(state)
});

const mapDispatch: MapDispatchToProps<{ login: typeof authLogin }, {}> = {
  login: authLogin
};

export const Login = connect(
  mapStateToProps,
  mapDispatch
)(({ auth, login }: { auth: AuthState; login: typeof authLogin }) => (
  <>
    <code>{JSON.stringify(auth.account)}</code>
    <h1>Login Page Works!</h1>
    <button
      onClick={() =>
        login({ accountName: "create-test14", password: "qwer1234qwer1234" })
      }
    >
      登录
    </button>
  </>
));
